import { NextRequest, NextResponse } from 'next/server'
import { jwtVerify } from 'jose'

const PUBLIC_PATHS = [
  '/api/auth/login',
  '/api/auth/register',
  '/api/auth/forgot-password',
  '/api/auth/reset-password',
  '/api/health',
  '/api/script',
  '/login',
  '/register',
  '/forgot-password',
  '/reset-password',
]

const CSRF_PROTECTED_METHODS = ['POST', 'PUT', 'DELETE', 'PATCH']

function isPublicPath(pathname: string): boolean {
  return PUBLIC_PATHS.some(path =>
    pathname === path || pathname.startsWith(path + '/')
  )
}

async function verifyToken(token: string): Promise<{ valid: boolean; payload?: Record<string, unknown> }> {
  const secret = process.env.JWT_SECRET
  if (!secret) {
    console.error('JWT_SECRET is not configured')
    return { valid: false }
  }

  try {
    const { payload } = await jwtVerify(
      token,
      new TextEncoder().encode(secret)
    )
    return { valid: true, payload: payload as Record<string, unknown> }
  } catch {
    return { valid: false }
  }
}

function verifyCsrfToken(request: NextRequest): boolean {
  const csrfHeader = request.headers.get('x-csrf-token')
  const csrfCookie = request.cookies.get('csrf-token')?.value

  if (!csrfHeader || !csrfCookie) {
    return false
  }

  return csrfHeader === csrfCookie
}

export async function middleware(request: NextRequest) {
  const { pathname } = request.nextUrl

  // Allow public paths
  if (isPublicPath(pathname)) {
    return NextResponse.next()
  }

  // Only protect API routes
  if (!pathname.startsWith('/api/')) {
    return NextResponse.next()
  }

  // Block test endpoints in production
  if (process.env.NODE_ENV === 'production' && pathname.startsWith('/api/test')) {
    return NextResponse.json({ error: 'Not found' }, { status: 404 })
  }

  // Verify JWT token from httpOnly cookie
  const authToken = request.cookies.get('auth-token')?.value

  if (!authToken) {
    return NextResponse.json({ error: 'Authentication required' }, { status: 401 })
  }

  const { valid, payload } = await verifyToken(authToken)

  if (!valid) {
    const response = NextResponse.json({ error: 'Invalid or expired token' }, { status: 401 })
    // Clear invalid token
    response.cookies.delete('auth-token')
    return response
  }

  // Verify CSRF token for state-changing requests
  if (CSRF_PROTECTED_METHODS.includes(request.method)) {
    if (!verifyCsrfToken(request)) {
      return NextResponse.json({ error: 'Invalid CSRF token' }, { status: 403 })
    }
  }

  // Add user info to request headers for downstream handlers
  const requestHeaders = new Headers(request.headers)
  requestHeaders.set('x-user-id', String(payload?.id || ''))
  requestHeaders.set('x-user-email', String(payload?.email || ''))

  return NextResponse.next({
    request: {
      headers: requestHeaders,
    },
  })
}

export const config = {
  matcher: [
    '/api/:path*',
    '/dashboard/:path*',
  ],
}
